<template>
    <div class="card">
        <div class="header">
            <img
                src="@/assets/utilitya.svg"
                style="width: 24px; height: 24px"
            />
            <h2 class="top_info_heading">Utility Chain</h2>
            <div class="headera">
                <transition name="blink" mode="out-in">
                    <span v-if="showIncomingText" class="subtexter"
                        >Incoming Transaction</span
                    >
                    <span v-else class="subtext">Recent Activity</span>
                </transition>
            </div>
        </div>
        <table>
            <thead>
                <tr>
                    <th>Hash</th>
                    <th>Amount</th>
                    <th>Timestamp</th>
                </tr>
            </thead>
            <transition name="slide" mode="out-in">
                <tbody>
                    <tr
                        v-for="transaction in Transactions"
                        :key="transaction.hash"
                    >
                        <td>{{ transaction.hash }}</td>
                        <td>
                            {{ formatValue(transaction.value) }} DJT
                            <img
                                src="@/assets/zuli.svg"
                                style="
                                    width: 20px;
                                    height: 18px;
                                    margin-left: 8px;
                                "
                            />
                        </td>
                        <td>{{ transaction.createdAt | fromNow }}</td>
                    </tr>
                </tbody>
            </transition>
        </table>
    </div>
</template>
<script>
export default {
    data() {
        return {
            Transactions: [],
            showIncomingText: false,
            previousLatestTransactionId: null,
        }
    },
    created() {
        this.fetchData()
        this.refreshInterval = setInterval(this.refreshData, 5000) // Refresh every 5 seconds
    },
    beforeDestroy() {
        clearInterval(this.refreshInterval)
    },
    methods: {
        async fetchData() {
            try {
                const response = await fetch(
                    'https://explorer.uksouth.cloudapp.azure.com/v2/ctransactions?limit=8&sort=timestamp-desc'
                )
                if (response.ok) {
                    const data = await response.json()
                    const latestTransactionId = data.Transactions[0].hash
                    const newTransactionAdded =
                        latestTransactionId !== this.previousLatestTransactionId
                    if (newTransactionAdded) {
                        this.showIncomingText = true
                        setTimeout(() => {
                            this.showIncomingText = false
                        }, 2000) // Display for 2 seconds
                    }

                    this.previousLatestTransactionId = latestTransactionId
                    this.Transactions = data.Transactions
                } else {
                    console.error('API request failed:', response.statusText)
                }
            } catch (error) {
                console.error('An error occurred:', error)
            }
        },
        formatDate(date) {
            return new Date(date).toLocaleString()
        },
        formatValue(value) {
            const etherValue = value / Math.pow(10, 18)
            return etherValue.toLocaleString(undefined, {
                minimumFractionDigits: 2,
                maximumFractionDigits: 2,
            })
        },
        refreshData() {
            this.fetchData()
        },
    },
}
</script>

