<template>
    <div class="card">
        <div class="header">
            <img src="@/assets/methoda.svg" style="width: 24px; height: 24px" />
            <h2 class="top_info_heading">Method Chain</h2>
            <div class="headera">
                <transition name="blink" mode="out-in">
                    <span v-if="showIncomingText" class="subtexter"
                        >Incoming Transaction</span
                    >
                    <span v-else class="subtext">Recent Activity</span>
                </transition>
            </div>
        </div>
        <table>
            <thead>
                <tr>
                    <th>Tx ID</th>
                    <th>Type</th>
                    <th>Timestamp</th>
                </tr>
            </thead>
            <tbody>
                <tr
                    v-for="transaction in transactionDisplayInfo"
                    :key="transaction.id"
                >
                    <td>{{ transaction.id }}</td>
                    <td>
                        {{ transaction.displayType }}
                        <img
                            :src="require(`@/assets/${transaction.icon}`)"
                            style="width: 20px; height: 18px; margin-left: 6px"
                            alt="Transaction Icon"
                        />
                    </td>
                    <td>{{ transaction.timestamp | fromNow }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
    data() {
        return {
            transactions: [],
            showIncomingText: false,
            previousLatestTransactionId: null,
        }
    },
    computed: {
        transactionDisplayInfo() {
            const typeMap = {
                add_delegator: {
                    display: 'Delegation',
                    icon: 'delegation-icon.svg',
                },
                pvm_import: {
                    display: 'IntraChain',
                    icon: 'methodintra-icon.svg',
                },
                add_validator: {
                    display: 'Validator',
                    icon: 'validator-icon.svg',
                },
                create_subnet: {
                    display: 'ECC',
                    icon: 'ecc-icon.svg',
                },
            }

            return this.transactions.map((transaction) => {
                const { display, icon } = typeMap[transaction.type] || {
                    display: transaction.type,
                    icon: 'default-icon.svg',
                }
                return {
                    ...transaction,
                    displayType: display,
                    icon,
                }
            })
        },
    },
    created() {
        this.fetchData()
        this.refreshInterval = setInterval(this.refreshData, 5000) // Refresh every 5 seconds
    },
    beforeDestroy() {
        clearInterval(this.refreshInterval)
    },
    methods: {
        async fetchData() {
            try {
                const response = await fetch(
                    'https://explorer.uksouth.cloudapp.azure.com/v2/transactions?chainID=11111111111111111111111111111111LpoYY&sort=timestamp-desc&limit=8'
                )
                if (response.ok) {
                    const data = await response.json()
                    const latestTransactionId = data.transactions[0].id
                    const newTransactionAdded =
                        latestTransactionId !== this.previousLatestTransactionId
                    if (newTransactionAdded) {
                        this.showIncomingText = true
                        setTimeout(() => {
                            this.showIncomingText = false
                        }, 2000) // Display for 2 seconds
                    }

                    this.previousLatestTransactionId = latestTransactionId
                    this.transactions = data.transactions
                } else {
                    console.error('API request failed:', response.statusText)
                }
            } catch (error) {
                console.error('An error occurred:', error)
            }
        },
        formatDate(date) {
            return new Date(date).toLocaleString()
        },
        formatValue(value) {
            const etherValue = value / Math.pow(10, 18)
            return etherValue.toLocaleString(undefined, {
                minimumFractionDigits: 2,
                maximumFractionDigits: 2,
            })
        },
        refreshData() {
            this.fetchData()
        },
    },
}
</script>

